import { isStaticProperty } from '@vue/compiler-core';
import axios from 'axios';
import { createStore } from 'vuex';

const store = createStore({
	state() {
		return {
			name: 'kim',
			age: 20,
			likes: 30,
			likesCLick: false,
			more: {},
		};
	},

	mutations: {
		// nameChange(state) {
		// 	state.name = 'park';
		// },
		// increment(state, payload) {
		// 	state.age += payload;
		// },

		setMore(state, data) {
			//dataÎäî setMoreÏì∏ Îïå Îç∞Ïù¥ÌÑ∞ ÎÑ£ÏùÑ Ïàò ÏûàÏùå!
			state.more = data;
		},

		likesPlus(state) {
			if (state.likesCLick == false) {
				state.likes++;
				state.likesCLick = true;
			} else {
				state.likes--;
				state.likesCLick = false;
			}
		},
	},

	// ajaxÌïòÎäî Í≥≥ or Ïò§ÎûòÍ±∏Î¶¨Îäî ÏûëÏóÖÎì§
	actions: {
		getData(context) {
			axios.get('https://codingapple1.github.io/vue/more0.json').then(a => {
				console.log(a.data);
				context.commit('setMore', a.data);
			});
		},
	},
});

export default store;
